/* eslint-disable react/prefer-stateless-function */
import React, { Component } from 'react';
import { NavLink } from 'react-router-dom';

import { activeFilter } from '../js/script.js';

class CatalogSideBarColor extends Component {
  componentWillMount() {
    this.colorClass(this.props.color);
  }

  componentWillReceiveProps(newProps) {
    this.colorClass(newProps.color);
  }

  getRef = node => {
    this.elem = node;
  };

  setFilter(event, filter) {
    activeFilter(event);
    this.props.updateFilter('color', filter);
  }

  colorClass = item => {
    let color = '';
    switch (item) {
      case '':
        color = 'black';
        break;
      case '':
        color = 'beige';
        break;
      case '':
        color = 'gray';
        break;
      case '':
        color = 'bardo';
        break;
      case '':
        color = 'white';
        break;
      case '':
        color = 'transparent';
        break;
      case '':
        color = 'darkblue';
        break;
      case '':
        color = 'red';
        break;
      case '-':
        color = 'darkgreen';
        break;
      case '':
        color = 'purple';
        break;
      case '':
        color = 'beige2';
        break;
      case '':
        color = 'orange';
        break;
      case '':
        color = 'metalic';
        break;
      case '':
        color = 'colorful';
        break;
      case '':
        color = 'brown';
        break;
      case '':
        color = 'silver';
        break;
      case '-':
        color = 'blackandwhite';
        break;
      case '':
        color = 'pink';
        break;
      default:
        color = '';
    }
    return color;
  };

  listColor(listColor) {
    if (!listColor) return null;
    return listColor.map((color, i) => (
      <li
        key={i}
        onClick={event => {
          this.setFilter(event, color);
        }}
      >
        <NavLink to="#">
          <div className={`color ${this.colorClass(color)}`} />
          <span className="color-name">{color}</span>
        </NavLink>
      </li>
    ));
  }

  render() {
    return (
      <section className="sidebar__division">
        <div className="sidebar__color sidebar__filter">
          <div className="sidebar__division-title">
            <h3></h3>
            <div
              className="opener-down"
              onClick={event => {
                this.props.opener(event, this.elem);
              }}
            />
          </div>
          <ul ref={this.getRef}>{this.listColor(this.props.color)}</ul>
        </div>
      </section>
    );
  }
}

export default CatalogSideBarColor;



