//  "" ()
import React, { Component } from 'react';

class HeartIcon extends Component {
  constructor(props) {
    super(props);
    this.state = {
      className: '',
    };
  }

  componentWillMount() {
    if (this.props.item) {
      this.setState({ className: this.classNameToFavorites(this.props.item) });
    }
  }

  componentWillReceiveProps(newProps) {
    this.setState({ className: this.classNameToFavorites(newProps) });
  }

  isFavorites = props => event => {
    event.preventDefault();
    if (localStorage.favorites === undefined) {
      //   localStorage.favorites,   .
      localStorage.setItem('favorites', JSON.stringify([props])); //  localStorage.localStorage
    } else {
      const arr = JSON.parse(localStorage.favorites); //  JSON
      let isItFavoriteProduct = true; // ,      
      for (let i = 0; i < arr.length; i++) {
        //  
        if (arr[i].id === props.id) {
          //  id   
          arr.splice(arr.map(item => item.id).indexOf(props.id), 1); //    
          isItFavoriteProduct = false;
        }
      }
      if (isItFavoriteProduct) {
        arr.unshift(props); //    
      }
      localStorage.setItem('favorites', JSON.stringify(arr)); //     localStorage
      this.setState({ className: this.classNameToFavorites(props) }); //   ""
      // console.log(this.props.update)
      // console.log(arr)
      if (this.props.updateState) {
        this.props.updateState(arr, true); //  state   
      }
    }
  };

  classNameToFavorites(props) {
    if (localStorage.favorites === undefined) {
      //   localStorage,
      localStorage.setItem('favorites', JSON.stringify([])); //   
    }
    const arrFavorites = JSON.parse(localStorage.favorites); //  JSON  
    const favorItem = arrFavorites.find(({ id }) => +props.id === +id); //   id  
    let className = favorItem
      ? 'product-catalogue__product_favorite-chosen'
      : 'product-catalogue__product_favorite'; //   id ,   
    if (!this.props.match) {
      //    ""
      return favorItem
        ? 'new-deals__product_favorite favorites'
        : 'new-deals__product_favorite';
    }
    if (this.props.match.url === '/favorites') {
      //    ""
      className = 'product-catalogue__product_favorite';
    }
    return className;
  }

  render() {
    return (
      <div
        className={this.state.className}
        onClick={this.isFavorites(this.props.item)}
      >
        <p />
      </div>
    );
  }
}
export default HeartIcon;



